import { evntBus } from "../../bus";
export default {
  // props: ["draftsDialog"],
  data: () => ({
    draftsDialog: false,
    singleSelect: true,
    selected: [],
    dialog_data: {},
    headers: [
      {
        text: "Customer",
        value: "customer",
        align: "start",
        sortable: true,
      },
      {
        text: "Date",
        align: "start",
        sortable: true,
        value: "posting_date",
      },
      {
        text: "Invoice",
        value: "name",
        align: "start",
        sortable: true,
      },
      {
        text: "Amount",
        value: "total",
        align: "start",
        sortable: false,
      },
    ],
  }),
  watch: {},
  methods: {
    close_dialog() {
      this.draftsDialog = false;
    },

    submit_dialog() {
      if (this.selected.length > 0) {
        evntBus.$emit("load_invoice", this.selected[0]);
        this.draftsDialog = false;
      }
    },
  },
  created: function () {
    evntBus.$on("open_drafts", (data) => {
      this.draftsDialog = true;
      this.dialog_data = data;
    });
  },
};
