import { evntBus } from "../bus";

export default {
  // components: {MyPopup},
  data() {
    return {
      drawer: true,
      mini: true,
      item: 0,
      items: [
        { text: "Dashboard", icon: "mdi-view-dashboard" },
        { text: "POS", icon: "mdi-point-of-sale" },
        { text: "Customer", icon: "mdi-account-box-multiple" },
        { text: "Closing", icon: "mdi-folder-open" },
      ],
      page: "",
      fav: true,
      menu: false,
      message: false,
      hints: true,
      menu_item: 0,
      snack: false,
      snackColor: "",
      snackText: "",
      company: "POS Awesome",
      company_img: "/assets/erpnext/images/erp-icon.svg",
    };
  },
  methods: {
    changePage(key) {
      this.$emit("changePage", key);
    },
    go_to() {
      frappe.set_route("workspace", "home");
      location.reload();
    },
    close_shift_dialog() {
      evntBus.$emit("open_closing_dialog");
    },
    show_mesage(data) {
      this.snack = true;
      this.snackColor = data.color;
      this.snackText = data.text;
    },
  },
  created: function () {
    this.$nextTick(function () {
      evntBus.$on("show_mesage", (data) => {
        this.show_mesage(data);
      });
      evntBus.$on("set_company", (data) => {
        this.company = data.name;
        this.company_img = data.company_logo ? data.company_logo :this.company_img ;
      });
    });
    
  },
};
